<template>
  <div>
    {{greetings}}
    <button @click="logout">Logout</button>
  </div>
</template>

<script>
import firebase from 'firebase'

export default {
  name: 'greetings',
  data: () => {
    return {
      greetings: 'Welcome inside!'
    }
  },

  methods: {
    logout () {
      firebase.auth().signOut().then(() => {
        this.$router.replace('login')
      })
    }
  }
}
</script>



